import React from "react";
import ReactDOM from "react-dom";

import { createMuiTheme, makeStyles, createStyles } from '@material-ui/core/styles';
import { Typography, Button, Grid, Paper, Switch } from "@material-ui/core";
import { ThemeProvider } from '@material-ui/styles';

import ContSlider from "./components/cont-slider"
import NavBar from "./components/nav-bar"
import { JsSynth } from './audio';

import { CitySynth, Param } from "C:/Users/Michael/Documents/rust/webby/wasm-game-of-life/Cargo.toml";

const theme = createMuiTheme(
    {
        typography: {
            // Use the system font instead of the default Roboto font.
            fontFamily: [
                '-apple-system',
                'BlinkMacSystemFont',
                '"Segoe UI"',
                'Roboto',
                '"Helvetica Neue"',
                'Arial',
                'sans-serif',
                '"Apple Color Emoji"',
                '"Segoe UI Emoji"',
                '"Segoe UI Symbol"',
            ].join(','),
            fontWeightMedium: 500,
            body1: {
                fontWeight: 500,
            },
            subtitle1: {
                fontSize: 12,
            },
            button: {
                fontStyle: 'italic',
            },
        },
    });

class App extends React.Component {
    constructor() {
        super();
        this.synth = CitySynth.new();
        window.synth = this.synth;
        this.jsSynth = new JsSynth(this.synth);
        this.state = {
            synthStarted: false
        };
    }

    update() {
        this.setState(() => {
            const synthState = Object.keys(Param).reduce((acc, k, i) => { acc[k] = this.synth.get_state(i); return acc; }, {});
            return {
                synthState
            };
        });
    }

    componentDidMount() {
        this.update();
    }

    static getMaxValue(param) {
        switch (param) {
            case "HarmonicsControl":
            case "HarmonicFix":
            case "HarmonicV1":
                return 1.0;
            case "HarmonicFunction":
            case "WFunction":
                return 5.0;
            case "LPF":
                return 5000.0;
            default:
                return 100.0;
        }
    }

    render() {
        /** @type React.CSSProperties */
        const gridContainer = { flexGrow: 0, flexDirection: 'row', padding: "15px 0px" };
        /** @type React.CSSProperties */
        const gridItem = { flexGrow: 0, textAlign: 'center', padding: "0px 8px" };

        /** @param {React.CSSProperties} style */
        const sliders = (style) => Object.keys(Param)
            .map((paramName, idx) => ({
                paramName,
                maxValue: App.getMaxValue(paramName),
                state: this.synth.get_state(idx),
                setState: value => this.synth.set_state(idx, value)
            }))
            .sort((first, second) => second.maxValue - first.maxValue)
            .map(({ paramName, maxValue, state, setState }) =>
                <Grid item xs={6} style={style} key={paramName}>
                    <Typography style={{flexGrow: 0}}>{paramName}</Typography>
                    {
                        maxValue === 1
                            ? <Switch
                                checked={state === 1.0}
                                onChange={event => { setState(event.target.checked ? 1.0 : 0.0); this.update() }}
                                style={{flexGrow: 1}}/>
                            : <ContSlider
                                initialValue={state}
                                maxValue={maxValue}
                                onChange={value => { setState(value); this.update() }} />
                    }
                </Grid>
            );

        return (
            <ThemeProvider theme={theme}>
                <div className="App" style={{ padding: "15px 25px" }}>
                    <Paper>
                        <Paper>
                            <NavBar content={
                                <Grid container style={gridContainer}>
                                    <Grid item style={gridItem}>
                                        <Button variant="contained" color="primary" size="small" disabled={this.state.synthStarted}
                                            onClick={() => { this.state.synthStarted || this.jsSynth.play(true); this.setState({ synthStarted: true }); }}>Play</Button>
                                    </Grid>
                                    <Grid item style={gridItem}>
                                        <Button variant="contained" color="secondary" size="small"
                                            onMouseDown={() => this.jsSynth.noteOn()} onMouseUp={() => this.jsSynth.noteOff()}>Note</Button>
                                    </Grid>
                                    <Grid item style={gridItem}>
                                        <Button variant="contained" color="secondary" size="small" onClick={() => this.jsSynth.noteOn()}>Note Held On</Button>
                                    </Grid>
                                    <Grid item style={gridItem}>
                                        <Button variant="contained" color="secondary" size="small" onClick={() => this.jsSynth.noteOff()}>Note Held Off</Button>
                                    </Grid>
                                    <Grid item style={gridItem}>
                                        <Button variant="contained" color="default" size="small" onClick={() => this.jsSynth.togglePeriodicNotes()}>Ping!</Button>
                                    </Grid>
                                    <Grid item style={gridItem}>
                                        <Button variant="contained" color="primary" size="small" onClick={() => this.jsSynth.refresh(true)}>Refresh</Button>
                                    </Grid>
                                    <Grid item style={gridItem}>
                                        <Button variant="outlined" color="default" size="small" onClick={() => console.table(this.state.synthState)}>Debug</Button>
                                    </Grid>
                                </Grid>
                            } />
                        </Paper>
                        <Paper>
                            <Grid container style={{ flexGrow: 1, width: "100%", padding: "15px 10px" }}>
                                {sliders({ flexGrow: 1, padding: "8px 25px", display: "flex", flexDirection: "column" })}
                            </Grid>
                        </Paper>
                    </Paper>
                </div >
            </ThemeProvider>
        )
    }
}

var mountNode = document.getElementById("app");
ReactDOM.render(<App />, mountNode);
